module.exports = "# Feedback banner\n\n## Props\n\n```table\nspan: 6\nrows:\n  - Prop: isOpen\n    Type: boolean\n    Default: \"false\"\n    Notes: Defines if banner is open or not. Required prop.\n  - Prop: isExpanded\n    Type: boolean\n    Default: \"false\"\n    Notes: Allows to open banner already expanded.\n  - Prop: onRequestClose\n    Type: function\n    Default: none\n    Notes: A callback function which gets executed when the close button is pressed.  No close button would be visible if this prop is not passed.\n  - Prop: heading\n    Type: string\n    Default: none\n    Notes: Heading text. Required prop.\n  - Prop: content\n    Type: string\n    Default: none\n    Notes: Content text\n  - Prop: linkText\n    Type: string\n    Default: none\n    Notes: Link text. Should be used with href.\n  - Prop: href\n    Type: string\n    Default: none\n    Notes: Specifies the URL of the page the link goes to. When `href` passed control element is an anchor element otherwise it is the expand/collapse button. Should NOT be used with onButtonClick, expandedText or collapsedText. Should be used with linkText\n  - Prop: linkProps\n    Type: object\n    Default: none\n    Notes: Link props when link control element is used. e.g. `target`\n  - Prop: onButtonClick\n    Type: function\n    Default: none\n    Notes: A callback function which gets executed when the expand/collapse button is clicked. This prop should NOT be used with href or linkText and should be passed when expand/collapse button is needed.\n  - Prop: expandedText\n    Type: string\n    Default: none\n    Notes: Expanded state control button text. Should be used with collapsedText and onButtonClick.\n  - Prop: collapsedText\n    Type: string\n    Default: none\n    Notes: Collapsed state control button text. Should be used with expandedText and onButtonClick.\n  - Prop: buttonProps\n    Type: object\n    Default: none\n    Notes: Expand/collapse button props when button control element is used.\n  - Prop: containerProps\n    Type: object\n    Default: none\n    Notes: Extra props passed to the banner container.\n  - Prop: contentProps\n    Type: object\n    Default: none\n    Notes: Extra props passed to the content wrapper.\n  - Prop: variant\n    Type: string\n    Default: none\n    Notes: Defines banner varian. Can be one of `error`, `success`, `alert`, `info`. If variant is not passed then icon should be passed and styles set manually.\n  - Prop: icon\n    Type: node\n    Default: none\n    Notes: Allows to pass a custom Icon\n```\n\n```react\n---\n<div>\n  <Banner\n    heading=\"This is your primary message text.\"\n    content=\"As a result, Audiences and other customer-related features in Fan are not    available. We are working to restore it as soon as possible. You can still work      with Pixels and CFCs during this time. Now is the time for all great men to come to  the aid of their party.\"\n    expandedText=\"expandedText\"\n    collapsedText=\"collapsedText\"\n    onButtonClick={() => {\n      console.log('onControlClick');\n    }}\n    isOpen\n    onRequestClose={() => {\n      console.log('onRequestClose');\n    }}\n    icon={\n      <ClearIcon size=\"large\">\n        <title>Home Icon</title>\n      </ClearIcon>\n    }\n  />\n  <Spacing bottom={{small: \"moderate\"}}/>\n  <Banner\n    heading=\"This is your primary message text.\"\n    content=\"As a result, Audiences and other customer-related features in Fan are not    available. We are working to restore it as soon as possible. You can still work      with Pixels and CFCs during this time. Now is the time for all great men to come to  the aid of their party.As a result, Audiences and other customer-related features in Fan are not    available. We are working to restore it as soon as possible. You can still work      with Pixels and CFCs during this time. Now is the time for all great men to come to  the aid of their party.\"\n    onButtonClick={() => {\n      console.log('onControlClick');\n    }}\n    isOpen\n    expandedText=\"expandedText\"\n    collapsedText=\"collapsedText\"\n    variant=\"error\"\n    isExpanded\n  />\n  <Spacing bottom={{small: \"moderate\"}}/>\n  <Banner\n    heading=\"This is your primary message text.\"\n    linkText=\"Link\"\n    href=\"https://www.ticketmaster.com/\"\n    linkProps={{ target: '_blank' }}\n    isOpen\n    variant=\"success\"\n    onRequestClose={() => {\n      console.log('onRequestClose');\n    }}\n  />\n  <Spacing bottom={{small: \"moderate\"}}/>\n  <Banner\n    heading=\"This is your primary message text.\"\n    linkText=\"Link\"\n    href=\"https://www.ticketmaster.com/\"\n    linkProps={{ target: '_blank' }}\n    isOpen\n    variant=\"alert\"\n  />\n  <Spacing bottom={{small: \"moderate\"}}/>\n  <Banner\n    heading=\"This is your primary message text.\"\n    linkText=\"Link\"\n    href=\"https://www.ticketmaster.com/\"\n    linkProps={{ target: '_blank' }}\n    isOpen\n    variant=\"info\"\n    onRequestClose={() => {\n      console.log('onRequestClose');\n    }}\n  />\n  <Spacing bottom={{small: \"moderate\"}}/>\n  <Banner\n    heading=\"This is your primary message text.\"\n    content=\"This is the content section\"\n    linkText=\"Link\"\n    href=\"https://www.ticketmaster.com/\"\n    linkProps={{ target: '_blank' }}\n    isOpen\n    variant=\"custom\"\n    expandedText=\"expandedText\"\n    collapsedText=\"collapsedText\"\n    isExpanded\n    customColors={{\n      primaryColor: \"#c56fff\",\n      backgroundColor: \"rgba(240, 218, 255, 0.4)\",\n      textColor: \"#262626\"\n    }}\n  />\n  <FunctionalBannerDemo />\n</div>\n```\n"

